.message{
    position: absolute;
    top: -50px;
    right: 50px;
    max-width: 250px;
    word-wrap: break-word;
    background-color: rgb(61, 61, 61, 1);
    border-radius: 5px;
    text-align: center;
    z-index: 4;
    opacity: 0;
    
    animation: anim_message 1s;
    animation-fill-mode: forwards;
}
.message p{
    color: white;
    padding: 15px;
    font-weight: 400;
}
@keyframes anim_message{
    0%{
        top: -50px;
        opacity: 0;
    }
    50%{
        top: 25px;

    }
    100%{
        top: 20px;
        opacity: 1;
    }
}

.hide_message{
    display: none;
}