:root{
    --back_yellow: rgb(255, 211, 17);
    --back_green: rgb(60, 245, 35);
    --icon_green_color: rgb(35, 190, 15);
    --hard_green: rgb(25, 126, 12);
    --light_yellow:  rgb(215, 238, 6);
}

.message_Container{
    position: relative;
    display: flex;
    align-items: center;
    width: 55%;
    background: rgb(236, 236, 236);
    border-radius: 15px;
    margin: 8px;
    padding: 8px;
    z-index: 11;
}
.message_isMine{
    background: rgb(125, 207, 30);
    margin-left: 45%;
}
.vue{
    position: absolute;
    right: 10px;
    bottom: 5px;
    font-size: 20px;

    transition: all .5s;
}
.vue .true{
    color: rgb(0, 238, 255);
    background: rgb(0, 78, 72);
    box-shadow: 0 0 15px rgb(0, 238, 255);
}
.vue .false{
    color: rgb(8, 124, 133);
    background: rgb(0, 78, 72);
    box-shadow: 0 0 15px rgb(8, 91, 97);
}

.message_data{
    margin-left: 8px;
}
.message_data_username{
    font-weight: 800;
}
.message_data_message{
    font-weight: 500;
    color: #252525;
}
.message_data_timeSend{
    color: rgb(116, 92, 92);
    font-weight: 700;
    justify-items: right;
}