.Loading_Search,
.Loading_send_message{
    transform: rotateZ(0deg);
    animation: anim_loading .8s infinite linear;
}
.Loading_get_message{
    position: absolute;
    top: 40%;
    left: 40%;
    font-size: 100px;
    padding: 10px;
    border-radius: 50px;
    color: rgb(0, 90, 90);

    transform: rotateZ(0deg);
    animation: anim_loading .6s infinite linear;
}

@keyframes anim_loading{
    0%{
        transform: rotateZ(0);
    }
    100%{
        transform: rotateZ(360deg);
    }
}