:root{
    --back_yellow: rgb(255, 211, 17);
    --back_green: rgb(60, 245, 35);
    --icon_green_color: rgb(35, 190, 15);
    --hard_green: rgb(25, 126, 12);
    --light_yellow:  rgb(215, 238, 6);
}

.logsign{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: flex;
    overflow: hidden;
    align-items: center;
    background: linear-gradient(to bottom, rgb(255, 211, 17), rgb(60, 245, 35));
    z-index: 1;
}
.logsign::after{
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 2;
    box-shadow: inset 10px 10px 140px rgba(0, 0, 0, 0.65), 
    inset -10px -10px 140px rgba(0, 0, 0, 0.65) ;
}

.logsign_container{
    position: relative;
    background-color: white;
    margin: auto;
    width: 400px;
    height: 370px;
    border-radius: 30px;
    box-shadow: -3px 8px 45px black;
    z-index: 3;
}

.message_icon{
    position: absolute;
    top: 100px;
    left: 50%;
    transform: translateX(-50%);
    font-size: 200px;
    color: rgb(35, 190, 15);

    animation: anim_icon 1s;
    animation-delay: .7s;
    animation-fill-mode: forwards;
}

@keyframes anim_icon{
    0%{
        top: 100px;
        font-size: 200px;
    }
    100%{
        top: -48px;
        font-size: 120px;
    }
}

/* LogSign */
.logsign_forms{
    background-color: transparent;
    position: absolute;
    top: 50%;
    left: 0;
    transform: translateY(-50%);
    display: flex;
    align-items: center;
    width: 800px;
    padding-top: 15px;
    opacity: 0;

    transition: all .5s;
    animation: anim_logsign_forms 1s;
    animation-delay: 1.6s;
    animation-fill-mode: forwards;
}
@keyframes anim_logsign_forms{
    0%{ opacity: 0; }
    100%{ opacity: 1; }
}

.logsign_forms_go_left{
    left: -400px;
}

.logsign_switcher{
    position: absolute;
    bottom: 12px;
    left: 50%;
    transform: translateX(-50%);
    color: rgb(25, 126, 12);
    font-weight: 500;
    text-decoration: underline;
    cursor: pointer;
}

/* login and register */
.login_form div,
.register_form div{
    display: flex;
    flex-direction: column;
    margin: 8px;
}
.login_form div label,
.register_form div label{
    color: rgb(92, 92, 92);
    margin: 8px 17px;
}
.login_form div input,
.register_form div input{
    padding: 7px;
    border: 1px solid rgb(139, 139, 139);
    margin: auto;
    width: 350px;
    background-color: rgb(228, 241, 201);
    font-size: 14px;
}
.login_form button,
.register_form button{
    background: linear-gradient(to right, rgb(215, 238, 6), rgb(25, 126, 12));
    outline-style: none;
    color: white;
    display: block;
    margin: 15px auto 0 auto;
    padding: 8px 28px;
    font-size: 20px;
    font-weight: 700;
    border-radius: 20px;
    border: none;
}
.login_form button:hover,
.register_form button:hover{
    cursor: pointer;
    box-shadow: 0 0 8px var(--hard_green);
    font-size: 20.5px;
}
.isLoading{
    opacity: .5;
    pointer-events: none;
}

/* login */
.login_form{
    width: 400px;
    text-align: start;
    opacity: 1;

    transition: all .4s ease-in;
}

/* register */
.register_form{
    width: 400px;
    text-align: start;
    opacity: 1;

    transition: all .4s ease-in;
}

.logsign_forms_hide{
    opacity: 0;
    pointer-events: none;
}

