:root{
    --back_yellow: rgb(255, 211, 17);
    --back_green: rgb(60, 245, 35);
    --icon_green_color: rgb(35, 190, 15);
    --hard_green: rgb(25, 126, 12);
    --light_yellow:  rgb(215, 238, 6);
}

.mainapp{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: linear-gradient(to bottom, rgb(255, 211, 17, .56), rgb(60, 245, 35, .56));
    overflow: hidden;
}

.deconect{
    position: absolute;
    top: 0;
    left: 30%;
    transform: translateX(-30%);
    background: #009688;
    padding: 4px 6px;
    border-radius: 0 0 20px 20px;
    color: white;
    font-weight: 600;
    cursor: pointer;
}
.deconect:hover{
    box-shadow: 0 0 5px rgb(58, 58, 58);
    background-color: #06bba9;
}

.message_box{
    position: relative;
    background-color: transparent;
    width: 40%;
    height: 90%;
    margin: 35px auto;
    border-radius: 20px;
    box-shadow: 0 4px 20px black;
}

.message_box .header{
    display: flex;
    height: 10%;
    justify-content: center;
    align-items: center;
    background-color: #009688;
    border-radius: 20px 20px 0 0;
    color: white;
    padding: 18px;
    border-bottom: 1px solid var(--hard_green);
}
.message_box .header .avatar{
    border: 3px solid var(--back_yellow);
}
.message_box .header p{
    margin-left: 15px;
    font-weight: 500;
    font-size: 20px;
}
.connect_dot{
    background-color: chartreuse;
    width: 15px;
    height: 15px;
    border-radius: 20px;
    margin-left: 5px;
}

/* Room Area */
.room_area{
    position: relative;
    display: flex;
    flex-direction: column;
    background-color: rgb(230, 179, 113);
    background: url('../images/banana_img.jpg');
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    height: 82%;
    overflow: hidden;
    overflow-y: scroll;
}

/* send message */
.send_message{
    display: flex;
    justify-content: center;
    align-items: center;
    height: 8%;
    background-color: #fcde57;
    border-radius: 0 0 20px 20px;
    border-top: 1px solid #6e601e;
}
.message_box input{
    width: 0;
    padding: 5px 15px;
    border-radius: 15px 0 0 15px;
    border: 1px solid #aa9538;
    outline: none;
    opacity: 0;

    animation: anim_input 1.5s;
    animation-fill-mode: forwards;
}
@keyframes anim_input{
    0%{ 
        width: 0;
        opacity: 0;
    }
    100%{ 
        width: 80%;
        opacity: 1;
    }
}
.send{
    color: white;
    font-weight: 600;
    padding: 5px;
    border: 1px solid #009688;
    width: 45px;
    border-radius: 0 15px 15px 0;
    background: #009688;
    outline: none;
    opacity: 0;

    animation: anim_button 1.5s;
    animation-fill-mode: forwards;
}
@keyframes anim_button{
    0%{ opacity: 0; }
    100%{ opacity: 1; }
}
.send:hover{
    background: #067a6f;
    cursor: pointer;
}
.send:active{
    background: #00554c;
}

.hide_sendMessage{
    display: none;
    pointer-events: none;
}

.noEvent_sendMessage{
    background: #36cec9e1;
    pointer-events: none;
    border: none;
}

/* media */
@media(max-width: 1200px){
    .message_box{
        width: 50%;
    }
}
@media(max-width: 800px){
    .message_box{
        width: 78%;
    }
}
@media(max-width: 700px){
    .message_box{
        width: 98%;
    }
}

/*  roooms */
.rooms{
    position: absolute;
    width: 160px;
    max-height: 440px;
    top: 30%;
    left: 0;
    background-color: #569e97;
    border-radius: 15px 0 0 15px;
    padding-bottom: 15px;

    transition: all .5s;
}
.rooms_go_left{
    left: -160px;
}
.rooms .search_input{
    position: relative;
    width: 100%;
    padding: 10px;
    border-radius: 15px 0 10px 0;
}
.rooms .drag_point{
    position: absolute;
    display: flex;
    top: 16px;
    left: -25px;
    text-align: center;
    justify-content: center;
    align-items: center;
    color: white;
    background: #009688;
    font-weight: 600;
    width: 25px;
    height: 20px;
    border-radius: 5px 0 0 5px;
    cursor: pointer;
}

.rooms .card{
    position: relative;
    background-color: rgb(218, 236, 218);
    display: flex;
    padding: 7px;
    padding-left: 15px;
    align-items: center;

    transition: all .4s ease-in-out;
}
.rooms .card::after{
    content: '';
    position: absolute;
    bottom: 0;
    left: 50%;
    transform: translateX(-50%);
    width: 88%;
    height: .5px;
    background-color: #01574e;
}
.rooms .card:last-child::after{
    display: none;
}
.rooms .card:hover{
    background-color: rgb(177, 211, 177);
    cursor: pointer;
}
.rooms .card:active{
    background-color: #01574e;
}
.rooms .card p{
    margin-left: 10px;
}

/* notification */
.notification_btn{
    position: absolute;
    display: flex;
    top: -70px;
    right: 1%;
    color: white;
    background: #2ee200;
    padding: 10px;
    font-size: 50px;
    z-index: 6;
    border-radius: 30px;

    transition: top .7s;
}
.notification_btn_go_down{
    top: 10px;
}
.notification_btn:hover{
    background: #36ee08;
    cursor: pointer;
}
.notification_btn:active{
    background: #25ac03;
}
.notification{
    position: absolute;
    top: 15%;
    right: -250px;
    width: 250px;
    max-height: 200px;
    background: #01574e;
    border-radius: 10px;
    overflow: hidden;
    overflow-y: scroll;
    z-index: 20;
    padding: 1px;

    transition: right 1s;
}
.notification_go_left{
    right: 0;
}

.notification_card{
    position: relative;
    display: flex;
    align-items: center;
    background: rgb(218, 236, 218);
    padding: 10px;
    padding-top: 15px;
    border-bottom: 1px solid #01574e;
    border-radius: 10px;

    transition: all .4s;
}
.notification_card:hover{
    background-color: rgb(177, 211, 177);
    cursor: pointer;
}
.notification_card:active{
    background-color: #01574e;
}
.notification_card .notification_messageNum{
    position: absolute;
    top: 20%;
    transform: translateY(-50%);
    left: 0;
    background: #2ee200;
    padding: 5px;
    color: white;
    border-radius: 50px;
}

.notification_card .notification_messages{
    padding: 5px;
    overflow: hidden;
    overflow-x: scroll;
}
.notification_card .notification_messages p{
    white-space: nowrap;
}
::-webkit-scrollbar{
    background: #00554c;
    height: 3px;
    width: 8px;
}
::-webkit-scrollbar-thumb{
    /* background: #b9b8b8; */
    background: linear-gradient(to right, rgb(12, 223, 12), rgb(247, 185, 15));
    border-radius: 10px 0 0 10px;
}

/* media */
@media(max-width: 800px){
    .rooms{
        width: 180px;
        top: 9.5%;
        left: 0;
        z-index: 20;
        background-color: #009688;
        border-radius: 0 0 15px 0;
    
        transition: all .5s;
    }
    .rooms .search_input{
        border-radius: 0 0 10px 0;
    }
    .rooms .drag_point{
        display: none;
    }
    .room_area{
        padding-top: 50px;
    }
}